import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import './Menu.css';

class Menu extends Component {
	constructor() {
		super();

		this.state = {
			categories: [],
			selectedCategory: ""
		}
	}

	selectFilter(category) {
		let paramCategory;
		if (this.state.selectedCategory === category) {
			paramCategory = "";
		} else  {
			paramCategory = category;
		}
		this.setState({ selectedCategory: paramCategory}, function() {
			this.props.filterPosts(this.state.selectedCategory);
		});
	}


	renderCategories() {
		const categories = [];
		this.props.posts ? this.props.posts.forEach((post, index, arr) => {
			if (categories.indexOf(post.fields.category) < 0) {
				categories.push(post.fields.category);
			}
		}) : [];
		const categoriesRender = categories.sort().reverse().map((category) => {
			return (
				<li>
					<a href={this.props.page === "info" ? "/" : null} className={ this.state.selectedCategory === category ? "selected" : "" } href="/#" onClick={ () => this.selectFilter(category) } >
						{ category }
					</a>
				</li>)
			;
		});
		return categoriesRender;
	}

	render() {
		return (
		  <div id="menu">
		  	<header>
			  	<img src="/images/logo.gif" />
			  	<div className="links">
			  		<ul>
			  			{
			  				this.renderCategories()
			  			}
			  			<li><a href="/info">?</a></li>
			  		</ul>
			  	</div>
			  	<div className="categories">
			  	</div>
		   	</header>
		  </div>
		);
	}
}

export default Menu;



