import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import BlogItem from './BlogItem';
import './Blog.css';

class Blog extends Component {
	constructor() {
		super();
	}

	renderPosts() {
		const postFeed = this.props.posts ? this.props.posts.map(post => {
      		return(<BlogItem post={ post } filtered={ this.props.filtered } />);
      	}) : null;
      	return postFeed;
	}

	render() {
		return (
		  <div className="blog">
		      {
		      	this.renderPosts()
		      } 
		  </div>
		);
	}
}

export default Blog;



