import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import * as contentful from 'contentful';
import * as Markdown from 'react-markdown';
import './Commercial.css';

class BlogItem extends Component {
	constructor() {
		super();
		this.state = {
			show: true
		}
	}

	componentDidUpdate(prevProps) {
		if (prevProps.filtered !== this.props.filtered) {
			this.toggleShowHide();
		}
	}
	
	toggleShowHide() {
		const category = this.props.post.fields.category;
		if (this.props.filtered === "" || this.props.filtered === category) this.setState({ show: true });
		else this.setState({ show: false });
	}


	render() {
		let videoId = this.props.post.fields.videoId;
  		let title = this.props.post.fields.title;
  		let client = this.props.post.fields.client;
  		let year = this.props.post.fields.year;
  		let description = this.props.post.fields.description;
  		let clientUrl = this.props.post.fields.clientUrl;
  		
		return (
          this.props.post.fields.category !== 'personal' ?
      		<div className={this.state.show ? "post-body" : "post-body hidden"}>

      			<div className="video">
      				<div style={{padding:'56.25% 0 0 0',position:'relative'}}><iframe src={'https://player.vimeo.com/video/' + videoId} style={{position:'absolute',top:'0',left:'0',width:'100%',height:'100%'}} frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe></div><script src="https://player.vimeo.com/api/player.js"></script>
      			</div>

      			<div className="post-content">
      				<div className="image-row">
	      				<div className="image-frame imageOne"><img src={this.props.post.fields.imageOne.fields.file.url} /></div>
	      				<div className="image-frame imageTwo"><img src={this.props.post.fields.imageTwo.fields.file.url} /></div>
      				</div>
      			</div>
      			<div className="post-info">
      				<div className="project-title">
      					{
      						(clientUrl && clientUrl !== "" ?
      						<div><a href={clientUrl} className="client">{client}:</a> {title} ({year})</div> :
      						<div>{client}: {title} ({year})</div>)
      					}
      				</div>
      				<div className="project-description">
                <Markdown source={ description } />
      				</div>
      			</div>
      		</div> : 

          <div className={this.state.show ? "post-body personal" : "post-body hidden"}>

            <div className="post-content personal">
              <div className="image-row">
                <div className="image-frame imageOne"><img src={this.props.post.fields.imageOne.fields.file.url} /></div>
                <div className="image-frame imageTwo"><img src={this.props.post.fields.imageTwo.fields.file.url} /></div>
              </div>
            </div>
            <div className="post-info">
              <div className="project-title">
                {
                  <div>{title} ({year})</div>
                }
              </div>
              <div className="project-description">
                <Markdown source={ description } />
              </div>
            </div>
          </div>

		);
	}
}

export default BlogItem;



