import React, { Component } from 'react';
import { withRouter} from 'react-router-dom';
import Menu from './Menu';
import * as Markdown from 'react-markdown';
import * as contentful from 'contentful';

import './Info.css';

class Info extends Component {
  constructor() {
      super();

      this.state = {
          indexContent: [],
          portfolioPosts: []
      }

      this.client = contentful.createClient({
          space: 'kqppwwuu42ep',
          accessToken: '54e2fe90df005a1109ff8c3f5c3502a6ff8cce7e8eb24b184bc16806fdbcb163',
          posts: []
      });

  }

  componentWillMount() {
      this.fetchContent();
      console.log("Meow");
  }

  fetchContent() {
      this.client.getEntries({
          content_type: 'infoPage'
      })
      .then(entries => {
          console.log(entries);
          this.setState({ indexContent: entries.items });
      });
      this.client.getEntries({
          content_type: 'portfolio'
      })
      .then(entries => {
          console.log(entries.includes.fields);
          this.setState({ portfolioPosts: entries.items });
      });
  }

  renderContent() {
      const postFeed = this.state.indexContent ? this.state.indexContent.map(post => {
          return(<Markdown source={ post.fields.content } />);
      }) : null;
      
      return postFeed;
  }

  renderImage() {
      return this.state.indexContent ? this.state.indexContent.map(post => {
          return(<img src={ post.fields.image.fields.file.url } />);
      }) : null;
  }



  render() {
      return (
          <div className="info">
            	<Menu posts={ this.state.portfolioPosts } page="info" />
                  <div className="info-body">
                      <div className="info-content">
                          { this.renderContent() }
                      </div>
                      <div className="info-image">
                          { this.renderImage() }
                      </div>
                  </div>
          </div>
      );
  }
}

export default withRouter(Info);



